import React from 'react';
// View  Text UI组件 -> compile react  小程序
import { View, Image } from '@tarojs/components';

export default class PostIndex extends React.Component {
  state = {
    posts: [{
      id: 1,
      imageUrl: 'https://img13.360buyimg.com/pop/s590x470_jfs/t1/159304/38/9262/86536/603c9d69E7655205d/e42612a547856c29.jpg.webp',
      title: 'title',
      author: 'author'
    }]
  }

  render() {
    return (
      <View className="container">
        {
          this.state.posts.map((post) => 
            <View className="card" key={post.id}>
              <Image mode="aspectFill"
                className="card-img-top"
                src={post.imageUrl}
              />
              <View className="card-title">{post.title}</View>
              <View className="card-subtitle">{post.author}</View>
            </View>
          )
        }
      </View>
    )
  }
}
